<template>
	<div class="payComplete">
		<van-nav-bar fixed  left-text="返回" left-arrow @click-left="goBack" />
		<div class="box">
			<van-button class="btns" round loading type="info" :loading-text="`正在跳转订单页面 ${times}s...`" />
		</div>
	</div>
</template>

<script>
	import "../assets/less/PayComplete.less"
	export default {
		name: "PayComplete",
		data() {
			return {
				times:5,
				timer:null
			}
		},
		created() {
			this.goOrderPage()
		},
		methods:{
			goBack(){
				this.$router.go(-1);
				clearInterval(this.timer)
			},
			goOrderPage(){
				this.timer=setInterval(()=>{
					this.times--
					if (this.times<0) {
						clearInterval(this.timer)
						return this.$router.push({name:"OrderPage"})
					}
				},1000)
			}
			
		}
		
	}
</script>
